import * as actionTypes from "../actions/actionTypes";
import { updateObject } from "../utility";

const initialState = {
    orders: [],
    totalProductPurchase: 0,
    loading: false,
    purchased: false,
    backProduct: false,
};

const purchaseInitCheckout = (state, action) => {
    const orderProducts = action.products.filter((el) => el.totalPurchase > 0);
    const updatedProducts = {
        orders: orderProducts,
        totalProductPurchase: action.totalProductPurchase,
        purchased: false,
        backProduct: true,
    };

    return updateObject(state, updatedProducts);
};

const purchaseProductStart = (state, action) => {
    return updateObject(state, { loading: false });
};

const purchaseProductSuccess = (state, action) => {
    return updateObject(state, {
        loading: false,
        purchased: true,
        backProduct: false,
    });
};

const purchaseProductFail = (state, action) => {
    return updateObject(state, { loading: false });
};

const setProductsPurchase = (state, action) => {
    return updateObject(state, {
        status: "ok",
        message: action.message,
        error: false,
        loading: false,
    });
};

const statusProductsPurchase = (state, action) => {
    return updateObject(state, {
        status: action.status,
    });
};

const reducer = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.PURCHASE_INIT_CHECKOUT:
            return purchaseInitCheckout(state, action);
        case actionTypes.PURCHASE_PRODUCT_START:
            return purchaseProductStart(state, action);
        case actionTypes.PURCHASE_PRODUCT_SUCCESS:
            return purchaseProductSuccess(state, action);
        case actionTypes.PURCHASE_PRODUCT_FAIL:
            return purchaseProductFail(state, action);
        case actionTypes.SET_PRODUCTS_PURCHASE:
            return setProductsPurchase(state, action);
        case actionTypes.STATUS_PRODUCTS_PURCHASE:
            return statusProductsPurchase(state, action);
        default:
            return state;
    }
};

export default reducer;
