import React from "react";
import { withRouter } from "react-router-dom";
import { useDispatch } from "react-redux";

import InputItem from "../components/Product/InputItem";
import ProductItem from "../components/Product/ProductItem";

import { addProduct, removeProduct } from "../store/actions";

function ListProductItem(props) {
    const dispatch = useDispatch();

    const addProductHandler = (id) => {
        let payload = {
            id: id,
        };
        dispatch(addProduct(payload));
    };

    const removeProductHandler = (id) => {
        let payload = {
            id: id,
        };
        dispatch(removeProduct(payload));
    };

    return (
        <>
            <span className="w-2/12 sm:w-1/12 p-2 bg-gray-200 hover:bg-gray-400 text-gray-800 text-center text-bold">
                {props.countering}
            </span>
            <ProductItem
                className="w-6/12 sm:w-8/12 p-2 text-gray-800 text-left text-bold"
                name={props.productId}
                productId={props.productId}
                product={props.product}
            ></ProductItem>
            <InputItem
                className="w-4/12 sm:w-3/12 justify-between text-center"
                max={props.max}
                name={`barang-${props.productId}`}
                productId={props.productId}
                quantity={props.quantity}
                value={props.value}
                productAdded={() => addProductHandler(props.productId)}
                productRemoved={() => removeProductHandler(props.productId)}
                adjust={props.adjust}
            ></InputItem>
        </>
    );
}

export default withRouter(ListProductItem);
