import * as actionTypes from "./actionTypes";
import { toast, Zoom } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

export const purchaseProductStart = () => {
    return {
        type: actionTypes.PURCHASE_PRODUCT_START,
    };
};

export const purchaseProductSuccess = (payload) => {
    return {
        type: actionTypes.PURCHASE_PRODUCT_SUCCESS,
    };
};

export const purchaseProductFail = (error) => {
    return {
        type: actionTypes.PURCHASE_PRODUCT_FAIL,
        error: error,
    };
};

export const purchaseProducts = (payload) => {
    return (dispatch) => {
        dispatch(purchaseProductStart());
        dispatch(purchaseProductSuccess());

        let message;
        message = <div>Selamat, Pembelian anda berhasil dilakukan.</div>;

        toast.success(message, {
            transition: Zoom,
            autoClose: 3000,
            hideProgressBar: true,
        });
    };
};

export const purchaseInit = (payload) => {
    return (dispatch) => {
        dispatch(purchaseInitCheckout(payload));

        let message;
        message = <div>Harap periksa kembali, barang pembelian anda.</div>;

        toast.warning(message, {
            transition: Zoom,
            autoClose: 3000,
            hideProgressBar: true,
        });
    };
};

export const purchaseInitCheckout = (payload) => {
    return {
        type: actionTypes.PURCHASE_INIT_CHECKOUT,
        products: payload.products,
        totalProductPurchase: payload.totalProductPurchase,
    };
};

export const setProductsPurchase = () => {
    return {
        type: actionTypes.SET_PRODUCTS_PURCHASE,
    };
};

export const initProductsPurchase = () => {
    return (dispatch) => {
        dispatch(setProductsPurchase());
    };
};

export const statusProductsPurchase = (status) => {
    return {
        type: actionTypes.STATUS_PRODUCTS_PURCHASE,
        status: status,
    };
};
