import React, { useEffect } from "react";
import { withRouter } from "react-router-dom";

import { useDispatch, useSelector } from "react-redux";

import Header from "../parts/Header";
import ListProductItem from "../parts/ListProductItem";
import Footer from "../parts/Footer";

import Loading from "../parts/Loading";

import { initProducts, statusProducts, purchaseInit } from "../store/actions";

function Pages1({ history }) {
    const dispatch = useDispatch();
    const PRODUCT = useSelector((state) => state.product);
    const ORDER = useSelector((state) => state.order);

    useEffect(() => {
        document.title = "Front End || Product List Page";
        window.scroll(0, 0);
        if (!ORDER.backProduct) {
            dispatch(statusProducts("loading"));
            dispatch(initProducts());
        }
    }, [dispatch, ORDER.backProduct]);

    const purchaseContinueHandler = () => {
        const payload = {
            products: PRODUCT.products,
            totalProductPurchase: PRODUCT.totalProductPurchase,
        };
        dispatch(purchaseInit(payload));
        history.push("/checkout");
    };

    return (
        <>
            {PRODUCT.status === "loading" && <Loading></Loading>}
            <div className="site-container">
                <Header counter={PRODUCT.totalProductPurchase}></Header>
                <main className="site-content">
                    <>
                        <div className="m-4">
                            <span className="text-gray-800 text-3xl font-extrabold">
                                Product List
                            </span>
                        </div>
                    </>
                    {PRODUCT.status === "error" && (
                        <>
                            <span className="text-center text-xl">
                                <h1>{PRODUCT.message}</h1>
                            </span>
                        </>
                    )}
                    {PRODUCT.status === "ok" &&
                        (PRODUCT.totalProduct > 0 ? (
                            <>
                                <div className="flex m-4 gap-2 text-center font-bold text-gray-800">
                                    <span className="w-2/12 sm:w-1/12 py-2 bg-gray-400">
                                        No
                                    </span>
                                    <span className="w-6/12 sm:w-8/12 py-2 bg-gray-400">
                                        Product
                                    </span>
                                    <span className="w-4/12 sm:w-3/12  py-2 bg-gray-400">
                                        Total
                                    </span>
                                </div>
                                {Object.values(PRODUCT.products)?.map?.(
                                    (item, index) => {
                                        return (
                                            <div
                                                className="ListProductItem hover:bg-blue-400 flex m-4 gap-2"
                                                key={index}
                                            >
                                                <ListProductItem
                                                    product={item.title}
                                                    productId={item.id}
                                                    value={
                                                        PRODUCT.products[index]
                                                            .totalPurchase
                                                    }
                                                    quantity={0}
                                                    max={99}
                                                    adjust={true}
                                                    countering={index + 1}
                                                ></ListProductItem>
                                            </div>
                                        );
                                    }
                                )}
                                <p className="m-5 text-gray-700 font-bold text-xl">
                                    Total Product : {PRODUCT.totalProduct}
                                </p>
                            </>
                        ) : (
                            <>
                                <p className="m-5 text-red-700 text-center text-2xl font-bold">
                                    Data Tidak Tersedia.
                                </p>
                                <p className="m-5 text-gray-700 font-bold text-xl">
                                    Total Product : {PRODUCT.totalProduct}
                                </p>
                            </>
                        ))}
                    <div className="flex justify-end m-5">
                        {PRODUCT.status === "ok" && (
                            <>
                                <button
                                    className="bg-blue-500 hover:bg-blue-700 focus:outline-none p-3 m-0 text-white font-medium rounded"
                                    onClick={purchaseContinueHandler}
                                >
                                    Checkout
                                </button>
                            </>
                        )}
                    </div>
                </main>
                <Footer></Footer>
            </div>
        </>
    );
}

export default withRouter(Pages1);
