import * as actionTypes from "./actionTypes";
import product from "../../constants/api/product";

export const addProduct = (payload) => {
    return {
        type: actionTypes.ADD_PRODUCT,
        productId: payload.id,
    };
};

export const removeProduct = (payload) => {
    return {
        type: actionTypes.REMOVE_PRODUCT,
        productId: payload.id,
    };
};

export const statusProducts = (status) => {
    return {
        type: actionTypes.STATUS_PRODUCTS,
        status: status,
    };
};

export const setProducts = (payload) => {
    return {
        type: actionTypes.SET_PRODUCTS,
        message: payload.message,
        products: payload.products,
    };
};

export const fetchProductsFailed = (payload) => {
    return {
        type: actionTypes.FETCH_PRODUCTS_FAILED,
        message: payload.message,
    };
};

export const initProducts = () => {
    return (dispatch) => {
        product
            .getProduct()
            .then((response) => {
                let payload = {
                    message: "Berhasil Mengunduh Data",
                    products: response.data,
                };
                dispatch(setProducts(payload));
            })
            .catch((error) => {
                let payload = {
                    message:
                        "Gagal Mengunduh Data, silahkan coba beberapa saat lagi",
                };
                dispatch(fetchProductsFailed(payload));
            });
    };
};
