import * as actionTypes from "../actions/actionTypes";
import { updateObject } from "../utility";

const initialState = {
    products: null,
    totalProduct: 0,
    totalProductPurchase: 0,
    status: "idle",
    message: "",
    fetch: true,
    error: false,
};

const addProduct = (state, action) => {
    let objIndex = state.products.findIndex(
        (obj) => obj.id === action.productId
    );

    state.products[objIndex].totalPurchase += 1;
    const updatedProducts = state.products;
    const updatedState = {
        products: updatedProducts,
        totalProductPurchase: state.totalProductPurchase + 1,
    };

    return updateObject(state, updatedState);
};

const removeProduct = (state, action) => {
    let objIndex = state.products.findIndex(
        (obj) => obj.id === action.productId
    );

    state.products[objIndex].totalPurchase -= 1;
    const updatedProducts = state.products;
    const updatedState = {
        products: updatedProducts,
        totalProductPurchase: state.totalProductPurchase - 1,
    };

    return updateObject(state, updatedState);
};

const setProducts = (state, action) => {
    return updateObject(state, {
        products: action.products.map((o) => {
            o.totalPurchase = 0;
            return o;
        }),
        totalProduct: action.products?.length ?? 0,
        totalProductPurchase: 0,
        status: "ok",
        message: action.message,
        error: false,
    });
};

const fetchProductsFailed = (state, action) => {
    return updateObject(state, {
        status: "error",
        message: action.message,
        error: true,
    });
};

const statusProducts = (state, action) => {
    return updateObject(state, {
        status: action.status,
    });
};

const reducer = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.ADD_PRODUCT:
            return addProduct(state, action);
        case actionTypes.REMOVE_PRODUCT:
            return removeProduct(state, action);
        case actionTypes.SET_PRODUCTS:
            return setProducts(state, action);
        case actionTypes.FETCH_PRODUCTS_FAILED:
            return fetchProductsFailed(state, action);
        case actionTypes.STATUS_PRODUCTS:
            return statusProducts(state, action);
        default:
            return state;
    }
};

export default reducer;
