import React, { useEffect } from "react";
import { withRouter, Redirect } from "react-router-dom";

import { useDispatch, useSelector } from "react-redux";

import Header from "../parts/Header";
import ListProductItem from "../parts/ListProductItem";
import Footer from "../parts/Footer";

import Loading from "../parts/Loading";

import {
    initProductsPurchase,
    statusProductsPurchase,
    purchaseProducts,
} from "../store/actions";

function Pages2({ history }) {
    const dispatch = useDispatch();

    const ORDER = useSelector((state) => state.order);

    useEffect(() => {
        document.title = "Front End || Checkout Page";
        window.scroll(0, 0);
        dispatch(statusProductsPurchase("loading"));
        dispatch(initProductsPurchase());
    }, [dispatch]);

    const purchaseHandler = () => {
        dispatch(purchaseProducts());
    };

    const checkoutCancelHandler = () => {
        history.goBack();
    };

    const purchasedRedirect = ORDER.purchased ? <Redirect to="/" /> : null;

    return (
        <>
            {purchasedRedirect}
            {ORDER.status === "loading" && <Loading></Loading>}
            <div className="site-container">
                <Header counter={ORDER.totalProductPurchase}></Header>
                <main className="site-content">
                    <>
                        <div className="m-4">
                            <span className="text-gray-800 text-3xl font-extrabold">
                                Product List
                            </span>
                        </div>
                    </>
                    {ORDER.status === "error" && (
                        <>
                            <span className="text-center text-xl">
                                <h1>{ORDER.message}</h1>
                            </span>
                        </>
                    )}
                    {ORDER.status === "ok" &&
                        (ORDER.totalProductPurchase > 0 ? (
                            <>
                                <div className="flex m-4 gap-2 text-center font-bold text-gray-800">
                                    <span className="w-2/12 sm:w-1/12 py-2 bg-gray-400">
                                        No
                                    </span>
                                    <span className="w-6/12 sm:w-8/12 py-2 bg-gray-400">
                                        Product
                                    </span>
                                    <span className="w-4/12 sm:w-3/12 py-2 bg-gray-400">
                                        Total
                                    </span>
                                </div>
                                {Object.values(ORDER.orders)?.map?.(
                                    (item, index) => {
                                        return (
                                            <div
                                                className="ListProductItem hover:bg-blue-400 flex m-4 gap-2"
                                                key={index}
                                            >
                                                <ListProductItem
                                                    product={item.title}
                                                    productId={item.id}
                                                    value={
                                                        ORDER.orders[index]
                                                            .totalPurchase
                                                    }
                                                    quantity={0}
                                                    max={99}
                                                    adjust={false}
                                                    countering={index + 1}
                                                ></ListProductItem>
                                            </div>
                                        );
                                    }
                                )}
                                <p className="m-5 text-gray-700 font-bold text-xl">
                                    Total Product : {ORDER.totalProductPurchase}
                                </p>
                            </>
                        ) : (
                            <>
                                <p className="m-5 text-red-700 text-center text-2xl font-bold">
                                    Data Tidak Tersedia.
                                </p>
                                <p className="m-5 text-gray-700 font-bold text-xl">
                                    Total Product : {ORDER.totalProductPurchase}
                                </p>
                            </>
                        ))}
                    <div className="flex justify-between m-5">
                        <button
                            className="bg-orange-500 hover:bg-orange-700 focus:outline-none p-3 m-0 text-white font-medium rounded"
                            onClick={checkoutCancelHandler}
                        >
                            Back
                        </button>
                        {ORDER.status === "ok" &&
                        ORDER.totalProductPurchase > 0 ? (
                            <>
                                <button
                                    className="bg-blue-500 hover:bg-blue-700 focus:outline-none p-3 m-0 text-white font-medium rounded"
                                    onClick={purchaseHandler}
                                >
                                    Beli
                                </button>
                            </>
                        ) : (
                            <></>
                        )}
                    </div>
                </main>
                <Footer></Footer>
            </div>
        </>
    );
}

export default withRouter(Pages2);
